@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	font-family: "Alegreya", sans-serif;
}

header {
	background: #0d6efd;
	color: #ffffff;
	padding: 15px;
	font-weight: bold;
	box-shadow: 0px 2px 8px #888888;
	/* font-size: 30px; */
}

.main-container {
	margin-top: 35px;
	display: block;
}

.pin-container {
	text-align: center;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
}

.pin-text {
	color: #0d6efd;
	margin: 25px;
	font-weight: bold;
}

#search-btn {
	margin: 30px 0px;
}

.checkbox-div {
	display: grid;
	grid-template-columns: repeat(4, 120px);
}

.form-check-label {
	border: 1px solid #0d6efd;
	border-radius: 10%;
	padding: 5px 20px;
	cursor: pointer;
	color: #0d6efd;
}

input:checked ~ label {
	background: #0d6efd;
	color: white;
	border: 1px solid #212529;
}


@media (max-width: 575.98px) {
	#date-input {
		margin-top: 10px;
	}

	.form-check-label {
		padding: 5px 12px;
	}

	.checkbox-div {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
	}
}
.alert-div {
    margin-top: 30px;
}

.card {
    box-shadow: 3px 4px 9px #888888;
    /* width: 350px; */
    height: 380px;
    overflow: auto;
}

.card-text {
    margin-top: 8px;
}

.card-img {
    opacity: 0.11;
    margin-top: 40px;
    max-width: 80%;
    height: auto;
}

.card-title {
    margin-top: 10px;
}

li {
    list-style-type: none;
    margin-bottom: 4px;
}

#book-btn {
    color: #fff;
    text-decoration: none;
}

.alert {
    left: 0;
    margin: auto;
    position: absolute;
    right: 0;
    line-height: normal;
}

@media (min-width: 575.98px) {
    /* total width */
    * ::-webkit-scrollbar {
        width: 6px;
        scrollbar-color: trasparent;
    }
    /* scrollbar itself */
    * ::-webkit-scrollbar-thumb {
        background-color: #c5c5c7;
        border-radius: 16px;
        border: 1px solid rgba(218, 213, 213, 0.952);
    }
    * ::-webkit-scrollbar-thumb:hover {
        background-color: #a0a0a5;
        border: 1px solid #f4f4f4;
    }
}

@media (max-width: 575.98px) {
    .card-text {
        margin-top: 4px;
    }
    li {
        margin-bottom: 2px;    
    }
    .card-img {
        margin-top: 60px;
        max-width: 80%;
        height: auto;
    }
}
